const isKeyEnter = event => ((event instanceof KeyboardEvent) && (event.keyCode === 13));

const isElement = element => (element && (element.nodeType === 1));

const isString = str => (str && (typeof str === 'string'));

const isFunction = (param) => {
  const res = param && (typeof param === 'function');
  return res;
};

const now = new Date();

const formatDate = (parameter) => {
  // parameter '2018/01/01''2018-01-01'Date
  // '2018-01-01'
  const date = (parameter && parameter instanceof Date)
    ? parameter
    : new Date(String(parameter));
  const result = (date.getTime()) ? date.toLocaleDateString().replace(/\//g, '-') : '';
  return result;
};

const formatTime = (date) => {
  if (!(date instanceof Date)) { throw new TypeError(`formatTime date${date}`); }
  const str = date.toTimeString().match(/^\S+/)[0];
  return str;
};

const isValidDate = (date) => {
  const result = date && date instanceof Date && date.getTime();
  return result;
};

const newPromise = () => new Promise(resolve => resolve(1));

const differDay = (datea, dateb) => {
  //   1datebdatea -1datebdatea 0
  if (!(datea instanceof Date) || !(dateb instanceof Date)) {
    throw new TypeError(`differDay ${datea}  ${dateb}`);
  }
  const dateaStart = datea;
  const datebStart = dateb;
  const msecs = datebStart.getTime() - dateaStart.getTime();
  const days = msecs / 86400000;
  return days;
};

const isEmptyString = (content) => {
  // Falsely
  const isFalsely =
    (content === null) ||
    (content === undefined) ||
    (content === false) ||
    (content === []) ||
    (content === {});
  const isEmpty = !String(content).match(/\S/);
  return isFalsely || isEmpty;
};

const divisio = (ary, callback) => {
  // callback((item)=>{})
  // 2callbacktrue
  if (!(ary instanceof Array)) { throw new TypeError(`divisio ${ary} Array`); }
  if (!(callback instanceof Function)) { throw new TypeError(`divisio ${callback} Function`); }
  const result = [[], []];
  ary.forEach((item) => {
    const allot = callback(item);
    if (allot) {
      result[0].push(item);
    } else {
      result[1].push(item);
    }
  });
  return result;
};

const flat = (ary, deep) => {
  // 
  if (!(ary instanceof Array)) { throw new TypeError(`flat ${ary} Array`); }
  if (deep && (!Number.isSafeInteger(deep) || deep < 1)) { throw new TypeError(`flat ${deep} Integerdeep < 1`); }
  let result = ary;
  const times = deep || 1;
  const core = (array) => {
    const container = [];
    array.forEach((item) => {
      if (item instanceof Array) {
        item.forEach(i => container.push(i));
      } else {
        container.push(item);
      }
    });
    return container;
  };
  for (let index = 0; index < times; index += 1) {
    result = core(result);
  }
  return result;
};

export default {
  isElement,
  isKeyEnter,
  isString,
  isFunction,
  isEmptyString,
  newPromise,
  formatDate,
  formatTime,
  now,
  isValidDate,
  differDay,
  divisio,
  flat,
};
